.qr {
  .sh {
    margin: 5% 35%;
    width: 30%;
    height: auto;
  }

  /* The Modal (background) */
  .modal,
  .modali {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 9999999999999999999999999999999999 !important; /* Sit on top */
    padding-left: 35%;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0, 0, 0); /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  }
  .pic {
    width: 80% !important;
    height: auto !important;
  }
  .modal-header {
    padding: 2px 16px;
    background-color: #337ab7;
    color: white;
  }
  .modal-header h2 {
    font-size: 1rem;
    text-align: center;
  }

  /* Modal Content/Box */
  .modal-content {
    background-color: #ffffff;
    position: fixed;
    z-index: 99999999 !important;
    margin: 15% 0; /* 15% from the top and centered */
    padding: 10px;
    border: 1px solid #888;
    width: auto;
    height: auto; /* Could be more or less, depending on screen size */
  }
  .profile {
    /*background-color: rgb(211, 207, 207);*/
    background-color: rgb(238, 238, 238);
    width: 90%;
    height: auto;
    padding-left: 5px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-style: ridge;
    border-color: rgba(128, 128, 128, 0.075);
    margin: 10px;
    margin-top: 15px;
  }

  .Tag-no {
    margin-bottom: 10px;
    background-color: rgba(32, 31, 31, 0.897);
    color: snow;
    font-weight: 550;
  }
  .info-part {
    display: grid;
    grid-template-columns: 40% 60%;
  }

  .student-image img {
    width: 100%;
    height: 100%;
  }
  .details {
    display: grid;
    grid-template-columns: auto auto;
    padding-left: 10px;
    font-size: 15px;
    color: black;
  }

  .details-answer {
    font-weight: 550;
    font-size: 14px;
  }
  .submit-part input {
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 10px;
    border-style: ridge;
    box-sizing: border-box !important;
    border-color: rgba(0, 0, 0, 0.397) !important;
  }
  .submit-part button {
    width: 100%;
    padding-top: 4px;
    padding-bottom: 4px;
  }
  .confirm-button {
    background-color: rgba(9, 214, 9, 0.801);
    color: snow;
    border: 0.5px solid rgba(3, 117, 3, 0.719);
  }
  .decline-button {
    background-color: rgba(238, 15, 60, 0.815);
    color: snow;
    border: 0.5px solid rgba(168, 3, 3, 0.719);
  }

  /* The Close Button */
  .close {
    color: rgb(255, 255, 255);
    float: right;
    font-size: 28px;
    font-weight: bold;
  }

  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
  /* Medium screens */
  @media only screen and (max-width: 1130px) {
    .modal-content,
    .modal-contenti {
      position: fixed !important;
      z-index: 9999999999999999999999999999999999;
      background-color: #ffffff;
      margin: 10% auto; /* 15% from the top and centered */
      padding: 10px;
      border: 1px solid #888;
      width: auto;
      height: auto; /* Could be more or less, depending on screen size */
    }
  }
  @media only screen and (max-width: 825px) {
    .modal,
    .modali {
      width: 100%;
      padding-left: 3%;
    }
    .modal-content,
    .modal-contenti {
      position: fixed;
      background-color: #ffffff;
      margin: 15% 17.5%; /* 15% from the top and centered */
      padding: 5px;
      border: 1px solid #888;
      width: 55% !important;
      height: auto; /* Could be more or less, depending on screen size */
    }
    .main-grid {
      grid-template-rows: 7vh auto auto;
    }
    .sec {
      width: 33.3%;
      padding: 0;
    }
  }
  @media only screen and (max-width: 565px) {
    .modal-content,
    .modal-contenti {
      position: fixed;
      background-color: #ffffff;
      margin: 25% 0; /* 15% from the top and centered */
      padding: 5px;
      border: 1px solid #888;
      width: 91% !important;
      height: auto; /* Could be more or less, depending on screen size */
    }
  }
}
